import React, { Component, PropTypes } from 'react'
import s from './CandidateRow.scss'

class CandidateRow extends Component {

  static propTypes = {
    name: PropTypes.string,
    party: PropTypes.string,
    state: PropTypes.string,
    image: PropTypes.string,
    mouseDown: PropTypes.func,
    selectable: PropTypes.bool
  };

  hasState () {
    if (this.props.state !== undefined) {
      return <span className={s.candidateName}>{this.props.state}</span>
    } else {
      return
    }
  }

  render () {
    if (this.props.selectable) {
      return (
        <div className={s.candidateRow} onMouseDown={() => this.props.mouseDown()}>
          <img className={s.candidatePicture} src={this.props.image}/>
          <div className={s.candidateInfo}>
            <span className={s.stateName}>{this.props.name}</span>
            <span className={s.candidateParty}>{this.props.party}</span>
          </div>
        </div>
      )
    } else {
      return (
        <div className={s.candidateRow2}>
          <img className={s.candidatePicture} src={this.props.image}/>
          <div className={s.candidateInfo}>
            <span className={s.stateName}>{this.props.name}</span>
            <span className={s.candidateParty}>{this.props.party}</span>
          </div>
        </div>
      )
    }
  }
}

export default CandidateRow



