import React, { Component, PropTypes } from 'react'
import s from './GameInfoBar.scss'
import {Motion, spring} from 'react-motion'
import { actions as gameActions } from '../../redux/modules/game'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'

const mapStateToProps = (state) => ({
  s: state.game
})

const mapDispatchToProps = (dispatch) => ({
  game: bindActionCreators(gameActions, dispatch)
})

class GameInfoBar extends Component {

  static propTypes = {
    className: PropTypes.string,
    loading: PropTypes.number,
    name: PropTypes.string,
    size: PropTypes.number,
    description: PropTypes.string,
    total_points: PropTypes.number,
    winners: PropTypes.array,
    entry: PropTypes.number,
    submit: PropTypes.object,
    game_id: PropTypes.string,
    selects: PropTypes.object,
    s: PropTypes.object,
    game: PropTypes.object
  };

  static contextTypes = {
    location: React.PropTypes.object,
    history: React.PropTypes.object
  };

  componentWillMount () {
    this.setState({modalIsOpen: false})
  }

  openModal (str) {
    this.setState({modalIsOpen: true, tab: str})
  }

  closeModal () {
    this.setState({modalIsOpen: false})
  }

  componentDidUpdate () {
    if (this.props.s.didSubmit === 1) {
      return this.context.history.pushState(null, '/games')
    }
  }

  render () {
    return (
      <div className={s.root}>
        {this.loading()}
      </div>
    )
  }

  renderBack () {
    if (this.state.modalIsOpen) {
      return s.demo0
    } else {
      return s.demo01
    }
  }

  renderBlock () {
    if (this.state.modalIsOpen) {
      return s.demo0Block
    }
    return s.demo01Block
  }

  renderClickAbleArea () {
    if (this.state.modalIsOpen) {
      return s.clickable
    }
    return s.demo01Block
  }

  renderSomething () {
    if (this.state.tab === 'entry') {
      return (
        <div className={s.blockDiv}>
          <span className={s.blockTitle}>ENTRY FEE</span>
          <span className={s.blockEntry}>{this.props.entry + ' Fantasy Pollster Points'}</span>
          <span className={s.blockDescription}>{'Once you submit your picks, ' + this.props.entry + ' Fantasy Pollster Points will be deducted from your account.'}</span>
        </div>
      )
    } else if (this.state.tab === 'winnings') {
      return (
        <div className={s.blockDiv}>
          <span className={s.blockTitle}>TOTAL WINNINGS</span>
          <span className={s.blockDescription}>Here's a list of payouts for each place.</span>
          {this.calculateWinningsRow()}
        </div>
      )
    } else if (this.state.tab === 'points') {
      return (
        <div className={s.blockDiv}>
          <span className={s.blockTitle}>TOTAL POSSIBLE POINTS</span>
          <span className={s.blockEntry}>{this.props.total_points + ' Points'}</span>
          <span className={s.blockDescription}>Add more predictions to increase your possible points. Each prediction correct adds to your actual score. Each prediction wrong will subtract from your actual score.</span>
        </div>
      )
    }
  }

  calculateWinningsRow () {
    var newwinners = this.props.winners
    for (var i = this.props.winners.length; i < this.props.size; i++) {
      newwinners.push(0)
    }
    return newwinners.map(function (winner, i) {
      return (
        <div className={s.winningsRow} key={i + 'winnersmappalcething'}>
          <span className={s.winningsPlace}>{this.placeNumbering(i + 1)}</span>
          <span className={s.winningsPoints}>{winner + ' FPP'}</span>
        </div>
      )
    }.bind(this))
  }

  placeNumbering (num) {
    switch (num) {
      case 1:
        return '1st'
      case 2:
        return '2nd'
      case 3:
        return '3rd'
      default:
        return num + 'th'
    }
  }

  submitButton () {
    if (this.props.s.didSubmit) {
      <div>
        <span>hi</span>
      </div>
    } else {
      <div className={s.doneBox} onClick={() => this.props.game.submitGame(this.props.game_id)} key={'yo' + this.props.s.didSubmit}>
        <span className={s.textdone}>SUBMIT</span>
       </div>
    }
  }

  loading () {
    if (this.props.loading === 0) {
      return (
        <div className={s.container}>
          <div className={s.title}>
          <div className={s.titleRow}>
          <div className={s.titleColumn}>
            <span className={s.gameTitle}>{this.props.name}</span>
            <span className={s.text}>{this.props.description}</span>
          </div>
          <div className={s.statsWrap}>
            <div className={s.stats} onClick={() => this.openModal('entry')}>
              <span className={s.gameStates}>ENTRY</span>
              <span className={s.statNum}>{this.props.entry}</span>
            </div>
            <div className={s.stats} onClick={() => this.openModal('winnings')}>
              <span className={s.gameStates}>WINNINGS</span>
              <span className={s.statNum}>{this.calculateWinnings(this.props.winners)}</span>
            </div>
            <div className={s.stats} onClick={() => this.openModal('points')}>
              <span className={s.gameStates}>POINTS</span>
              <span className={s.statNum}>{'+/-' + this.props.total_points}</span>
            </div>
            <div className={s.doneBox} onClick={() => this.props.submit.submitGame(this.props.game_id)}>
              <span className={s.textdone}>SUBMIT</span>
             </div>
          </div>
          </div>
          <span className={s.infoText}>OBJECTIVE</span>
          <span className={s.text}>Accurately predict the winners of each primary using statistical evidence. Points are weighted by statistical probability of predicting correctly. Chain predictions together to increase possible points. The more points you earn, the higher you will place.</span>
          </div>
          <Motion style={{x: spring(this.state.modalIsOpen ? 150 : -400)}}>

          {({x}) =>
            <div className={this.renderBack()}>
            <div className={this.renderClickAbleArea()} onClick={() => this.closeModal()} />
            <div className={this.renderBlock()} style={{
              WebkitTransform: `translate3d(0, ${x}px, 0)`,
              transform: `translate3d(0, ${x}px, 0)`
            }}>
              {this.renderSomething()}
            </div>
            </div>
          }
        </Motion>

        </div>
     )
    }
  }

  calculateWinnings (winners) {
    var sum = 0
    for (var i = 0; i < winners.length; i++) {
      sum += winners[i]
    }
    return sum
  }

}
export default connect(mapStateToProps, mapDispatchToProps)(GameInfoBar)



