import React, { Component, PropTypes } from 'react'
import s from './SelectBox.scss'
import CandidateRow from '../CandidateRow/CandidateRow'
import { connect } from 'react-redux'
import { actions as gameActions } from '../../redux/modules/game'
import { bindActionCreators } from 'redux'

const mapStateToProps = (state) => ({
  every_selection: state.game.every_selection
})

const mapDispatchToProps = (dispatch) => ({
  game: bindActionCreators(gameActions, dispatch)
})

class SelectBox extends Component {

  constructor (props) {
    super(props)
    this.state = {
      candidate: '',
      place: '',
      statename: '',
      candidateDropdown: false,
      placeDropdown: false,
      stateDropDown: false,
      image: '/photos/candidates/fanny40x40.png',
      error: ''
    }
    this.candidatesDropdownFocus = this.candidatesDropdownFocus.bind(this)
    this.statesDropdownFocus = this.statesDropdownFocus.bind(this)
    this.handleKeys = this.handleKeys.bind(this)
  }
  static propTypes = {
    game: React.PropTypes.object.isRequired,
    name: PropTypes.string,
    date: PropTypes.string,
    state: PropTypes.string,
    image: PropTypes.string,
    states: PropTypes.array,
    statesMap: PropTypes.object,
    candidates: PropTypes.array,
    candidatesMap: PropTypes.object,
    isBottom: PropTypes.bool,
    isTop: PropTypes.bool,
    chainId: PropTypes.number,
    every_selection: PropTypes.object
  };

  getDay (date) {
    switch (date.getDay()) {
      case 0:
        return 'Sunday'
      case 1:
        return 'Monday'
      case 2:
        return 'Tuesday'
      case 3:
        return 'Wednesday'
      case 4:
        return 'Thursday'
      case 5:
        return 'Friday'
      case 6:
        return 'Saturday'
    }
  }

  getMonth (date) {
    switch (date.getMonth()) {
      case 0:
        return 'January'
      case 1:
        return 'February'
      case 2:
        return 'March'
      case 3:
        return 'April'
      case 4:
        return 'May'
      case 5:
        return 'June'
      case 6:
        return 'July'
      case 7:
        return 'August'
      case 8:
        return 'September'
      case 9:
        return 'October'
      case 10:
        return 'November'
      case 11:
        return 'December'
    }
  }

  logChange (val) {
    console.log('Selected: ' + val)
  }

  renderCandidatesDropDown () {
    if (this.state.candidateDropdown) {
      return (
        <div className={s.candidateDropDown}>
          {this.renderCandidates()}
        </div>
      )
    }
  }

  renderCandidates () {
    return this.props.candidates.map(function (candidate) {
      var candidatefound = this.props.candidatesMap.get(candidate)
      return (
        <CandidateRow selectable key={candidatefound._id} name={candidatefound.name} image={candidatefound.img} mouseDown={() => this.candidatesMouseDown(candidatefound)}/>
      )
    }.bind(this))
  }

  candidatesMouseDown (candidate) {
    return this.setState({image: candidate.img, candidate: candidate.name})
  }

  candidatesDropdownFocus () {
    if (!this.state.candidateDropdown) {
      return this.setState({candidateDropdown: true})
    } else {
      return this.setState({candidateDropdown: false})
    }
  }

  renderPlacesDropDown () {
    if (this.state.placeDropdown) {
      return (
        <div className={s.placeDropDown}>
          {this.renderPlaces()}
        </div>
      )
    }
  }

  renderPlaces () {
    var count = 0
    return this.props.candidates.map(function (candidate) {
      count = count + 1
      var c = count
      return (
        <div key={candidate} className={s.placeDiv} onMouseDown={() => this.setState({place: this.places(c)})}>
          <span className={s.dropDownText}>{this.places(count)}</span>
        </div>
      )
    }.bind(this))
  }

  places (i) {
    switch (i) {
      case 1:
        return '1st'
      case 2:
        return '2nd'
      case 3:
        return '3rd'
      case 4:
        return '4th'
      case 5:
        return '5th'
      case 6:
        return '6th'
      case 7:
        return '7th'
      case 8:
        return '8th'
      case 9:
        return '9th'
      case 10:
        return '10th'
      case 11:
        return '11th'
      case 12:
        return '12th'
      case 13:
        return '13th'
      case 14:
        return '14th'
      case 15:
        return '15th'
      case 16:
        return '16th'
      case 17:
        return '17th'
      case 18:
        return '18th'
      case 19:
        return '19th'
      case 20:
        return '20th'
    }
  }

  placesDropdownFocus () {
    if (!this.state.placeDropdown) {
      return this.setState({placeDropdown: true})
    } else {
      return this.setState({placeDropdown: false})
    }
  }

  renderStatesDropDown () {
    if (this.state.stateDropdown) {
      return (
        <div className={s.stateDropDown}>
          {this.populateStateDropdownDivs()}
        </div>
      )
    }
  }

  populateStateDropdownDivs () {
    return this.props.states.map(function (state) {
      var statefound = this.props.statesMap.get(state)
      return (
        <div key={statefound._id} className={s.placeDiv} onMouseDown={() => this.setState({statename: statefound.name})}>
          <span className={s.dropDownText}>{statefound.name}</span>
        </div>
      )
    }.bind(this))
  }

  statesDropdownFocus () {
    if (!this.state.stateDropdown) {
      return this.setState({stateDropdown: true})
    } else {
      return this.setState({stateDropdown: false})
    }
  }

  isBottom () {
    if (this.props.isBottom) {
      return s.leftSpotBottom
    }
    return s.leftSpot
  }

  render () {
    return (
      <div className={s.container}>
          <div className={s.candidateSelect}>
          <div className={s.candidateInput}>
            <img className={s.picture} src={this.state.image}/>
            <input value={this.state.candidate} onFocus={() => this.candidatesDropdownFocus()} onBlur={() => this.candidatesDropdownFocus()} placeholder={'Candidate'} type={'candidate'} className={s.stateInput}></input>
          </div>
          {this.renderCandidatesDropDown()}
          </div>
          <div className={s.placeSelect}>
          <input value={this.state.place} onFocus={() => this.placesDropdownFocus()} onBlur={() => this.placesDropdownFocus()} placeholder={'1st'} type={'position'} className={s.stateInput}></input>
          {this.renderPlacesDropDown()}
          </div>
          <div className={s.stateSelect}>
          <input onChange={this.handleKeys} onFocus={() => this.statesDropdownFocus()} onBlur={() => this.statesDropdownFocus()} value={this.state.statename} placeholder={'State'} type={'state'} className={s.stateInput}></input>
          {this.renderStatesDropDown()}
          </div>
          <div className={s.rightSide}>
            {this.renderError()}
            <div onClick={() => this.topPush()} className={s.clearDiv}>
              <span className={s.clearText}>ENTER</span>
            </div>
          </div>
      </div>
    )
  }

  renderError () {
    if (this.state.error !== '') {
      return (
        <span className={s.errorText}>{this.state.error}</span>
      )
    }
  }

  topPush () {
    var sentence = this.state.candidate + ' wins ' + this.state.place + ' in ' + this.state.statename
    var right = this.state.place + ' in ' + this.state.statename
    var left = this.state.candidate + this.state.statename
    if (this.state.candidate === '' || this.state.place === '' || this.state.statename === '') {
      return this.setState({error: 'Please complete your prediction.'})
    } else if (Object.keys(this.props.every_selection).length === 0) {
      this.props.game.addFromTop(this.state.image, this.state.candidate, this.state.place, this.state.statename)
    } else {
      if (this.props.every_selection.has(sentence)) {
        return this.setState({error: 'Prediction already chosen.'})
      } else if (this.props.every_selection.has(right)) {
        return this.setState({error: 'Position/State already chosen.'})
      } else if (this.props.every_selection.has(left)) {
        return this.setState({error: 'Candidate/State combination already chosen.'})
      } else {
        if (this.props.isTop) {
          this.props.game.addFromTop(this.state.image, this.state.candidate, this.state.place, this.state.statename)
        } else {
          this.props.game.addFromChain(this.props.chainId, this.state.image, this.state.candidate, this.state.place, this.state.statename)
        }
      }
    }
    this.setState({
      candidate: '',
      place: '',
      statename: '',
      candidateDropdown: false,
      placeDropdown: false,
      stateDropDown: false,
      image: '/photos/candidates/fanny40x40.png',
      error: ''
    })
  }

  handleKeys (e) {
    this.setState({statename: e.target.value})
  }

  renderDate (date) {
    var d = new Date(date)
    var month = this.getMonth(d)
    var dd = d.getDate()
    var s = month + ' ' + dd
    return s
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SelectBox)



