import React from 'react'
import s from './HomeView.scss'
import Header from '../../components/Header/Header.js'
import Footer from '../../components/Footer/Footer.js'
// We define mapStateToProps where we'd normally use
// the @connect decorator so the data requirements are clear upfront, but then
// export the decorated component after the main class definition so
// the component can be tested w/ and w/o being connected.
// See: http://rackt.github.io/redux/docs/recipes/WritingTests.html

export class HomeView extends React.Component {

  render () {
    return (
      <div>
        <div className={s.container} >
          <Header fixed home/>
          <div className={s.top}>
            <span className={s.title}>FantasyPollster</span>
            <span className={s.slogan}>Predict elections, make money</span>
          </div>
          <div className={s.top3}>
            <span className={s.slogan2}>FantasyPollster is the first statistics competition for the United States elections. Use your own statisical evidence to compete with others online. The most accurate players are guranteed to win cash.</span>
          </div>
          <div className={s.top2}>
            <span className={s.title2}>Paid games, great prizes</span>
            <span className={s.slogan}>Put your knowledge to the test and win real money playing against real people. The rules are simple: join a game, set your predictions, and the most accurate players win.</span>
          </div>
          <div className={s.top2}>
            <span className={s.title2}>Free games, too!</span>
            <span className={s.slogan}>Don’t worry, you don’t have to use real money. If you are just in this for the fame and glory, you can play a free game instead. Although there are no cash prizes, free game winners get eternal bragging rights (...or at least until 2020).</span>
          </div>
          <div className={s.top2}>
            <span className={s.title2}>Completely legal</span>
            <span className={s.slogan}>We have taken great pain to make sure our games are compliant with US law. This is not a gambling website, nor is it a weird financial scheme. We offer fun, predictive, skill-based competitions with static entry fees and static prizes.</span>
          </div>
          <div className={s.top2}>
            <span className={s.title2}>Open Sourced</span>
            <span className={s.slogan}>Prediction markets have been <a href='/points'>proven</a> to provide very accurate forecasts for presidential elections. That is why we are releasing the anonymized data from all of our closed games*. And, of course, it is completely free (as in speech and beer).</span>
          </div>
          <div className={s.top4}>
            <span className={s.footNote}>* Games “close” once they have been completely filled or are passed their final join date. We do not release predictions from games before they close in order ensure a fair experience for all players.</span>
          </div>
        </div>
        <Footer />
      </div>
    )
  }
}

export default HomeView



