export const logout = () => {
  return dispatch => {
    localStorage.removeItem('userToken')
    dispatch(removeData())
  }
}

export const getBalance = () => {
  return dispatch => {
    var getFoos = fetch('https://api.fantasypollster.com/api/users/balance', {
      headers: {
        'Authorization': 'Bearer ' + localStorage.getItem('userToken')
      },
      method: 'GET',
      cache: false
    })
    getFoos.then((response) => response.json()).then((json) => dispatch(balance(json.balance)))
  }
}

export const actions = {
  createLock,
  getProfile,
  logout,
  getBalance
}

// ------------------------------------
// Reducer
// ------------------------------------
export default handleActions({
  [SET_TOKEN]: (state, action) => (Object.assign({}, state, {
    idToken: action.payload,
    error: false
  })),
  [SET_PROFILE]: (state, action) => (Object.assign({}, state, {
    profile: action.payload,
    error: false
  })),
  [SET_LOCK]: (state, action) => (Object.assign({}, state, {
    lock: action.payload
  })),
  [SET_ERROR]: (state, action) => (Object.assign({}, state, {
    error: action.payload
  })),
  [BALANCE]: (state, action) => (Object.assign({}, state, {
    balance: action.payload
  })),
  [REMOVE_DATA]: (state, action) => (Object.assign({}, state, {
    idToken: '',
    profile: {},
    lock: {},
    balance: 0
  }))
}, {profile: {}, idToken: '', lock: {}, error: false, balance: 0})



