import React from 'react'
import { connect } from 'react-redux'
import { actions as gamesActions } from '../../redux/modules/games'
import s from './GameListView.scss'
import Header from '../../components/Header/Header.js'
import { actions as mygamesActions } from '../../redux/modules/mygames'
import { actions as profileActions } from '../../redux/modules/profile'
import { bindActionCreators } from 'redux'
import Footer from '../../components/Footer/Footer.js'

// We define mapStateToProps where we'd normally use
// the @connect decorator so the data requirements are clear upfront, but then
// export the decorated component after the main class definition so
// the component can be tested w/ and w/o being connected.
// See: http://rackt.github.io/redux/docs/recipes/WritingTests.html
const mapStateToProps = (state) => ({
  games: state.games,
  mygames: state.mygames,
  profile: state.profile
})

const mapDispatchToProps = (dispatch) => ({
  gamesActions: bindActionCreators(gamesActions, dispatch),
  mygamesActions: bindActionCreators(mygamesActions, dispatch),
  profileActions: bindActionCreators(profileActions, dispatch)
})

export class GameListView extends React.Component {

  static propTypes = {
    games: React.PropTypes.object.isRequired,
    mygames: React.PropTypes.object.isRequired,
    profile: React.PropTypes.object.isRequired,
    gamesActions: React.PropTypes.object.isRequired,
    mygamesActions: React.PropTypes.object.isRequired,
    profileActions: React.PropTypes.object.isRequired
  };

  static contextTypes = {
    history: React.PropTypes.object.isRequired
  };

  static childContextTypes = {
    history: React.PropTypes.object.isRequired
  };

  componentWillMount () {
    this.props.gamesActions.forceFetchGames()
  }

  componentDidMount () {
    this.props.mygamesActions.fetchMyGames()
  }

  month (date) {
    switch (date.getMonth()) {
      case 0:
        return 'January'
      case 1:
        return 'February'
      case 2:
        return 'March'
      case 3:
        return 'April'
      case 4:
        return 'May'
      case 5:
        return 'June'
      case 6:
        return 'July'
      case 7:
        return 'August'
      case 8:
        return 'September'
      case 9:
        return 'October'
      case 10:
        return 'November'
      case 11:
        return 'December'
    }
  }

  day (date) {
    switch (date.getDate()) {
      case 1:
        return '1st'
      case 2:
        return '2nd'
      case 3:
        return '3rd'
      case 4:
        return '4th'
      default:
        return date.getDate() + 'th'
    }
  }

  renderMyGames () {
    if (this.props.mygames.items.length > 0) {
      return (
        <div className={s.container}>
          <div key = {'gamekey'} className={s.rowContainerKey}>
            <span className={s.nameKey}>My Games</span>
            <span className={s.partyKey}>{'PARTY'}</span>
            <span className={s.entryKey}>{'RELEASE DATE'}</span>
            <span className={s.prizeKey}>{'WINNINGS'}</span>
            <span className={s.sizeKey}>{'SIZE'}</span>
          </div>
          {this.mapMyGamesRows()}
        </div>
      )
    }
  }

  handleTransition (id, entry) {
    if (this.props.profile.balance < entry && Object.keys(this.props.profile.profile).length !== 0) {
      this.context.history.push('/points')
    } else if (this.props.profile.idToken !== null && this.props.profile.idToken !== '') {
      this.context.history.push('/games/' + id)
    } else {
      this.props.profile.lock.show({
        icon: 'https://s3-us-west-2.amazonaws.com/static-assets-fanpol/sadcyclops.png',
        primaryColor: '#5c666f',
        socialBigButtons: true})
    }
  }

  mapMyGamesRows () {
    return this.props.mygames.items.map(function (game) {
      var g = this.props.games.mappedItems.get(game.match)
      var date = new Date(g.checkdate)
      return <div key = {game._id} className={s.rowContainer} onClick={() => this.handleTransition(game.match, -1)}>
                <span className={s.name}>{g.name}</span>
                <span className={s.party}>{g.party}</span>
                <span className={s.entry}>{this.month(date) + ' ' + this.day(date)}</span>
                <span className={s.prize}>{this.calculateWinnings(g.winners) + ' FPP'}</span>
                <span className={s.size}>{g.entries + '/' + g.maxsize}</span>
            </div>
    }.bind(this))
  }

  render () {
    return (
      <div className={s.root}>
        <Header key={'header1'} fixed={false}/>
        <Header key={'header2'} fixed/>
        <div className={s.container}>
          <div key = {'gamekey'} className={s.rowContainerKey}>
            <span className={s.nameKey}>Open Games</span>
            <span className={s.partyKey}>{'PARTY'}</span>
            <span className={s.entryKey}>{'ENTRY FEE'}</span>
            <span className={s.prizeKey}>{'WINNINGS'}</span>
            <span className={s.sizeKey}>{'SIZE'}</span>
          </div>
        {this.mapRows()}
        </div>
        {this.renderMyGames()}
        <Footer />
      </div>
    )
  }

  mapRows () {
    return this.props.games.items.map(function (game) {
      return <div key = {game._id} className={s.rowContainer} onClick={() => this.handleTransition(game._id, game.entry)}>
                <span className={s.name}>{game.name}</span>
                <span className={s.party}>{game.party}</span>
                <span className={s.entry}>{game.entry + ' FPP'}</span>
                <span className={s.prize}>{this.calculateWinnings(game.winners) + ' FPP'}</span>
                <span className={s.size}>{game.entries + '/' + game.maxsize}</span>
            </div>
    }.bind(this))
  }

  calculateWinnings (winners) {
    var sum = 0
    for (var i = 0; i < winners.length; i++) {
      sum += winners[i]
    }
    return sum
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(GameListView)



