import React, { Component, PropTypes } from 'react'
import s from './SelectedBox.scss'
import CandidateRow from '../CandidateRow/CandidateRow'

class SelectedBox extends Component {

  static propTypes = {
    name: PropTypes.string.isRequired,
    place: PropTypes.string.isRequired,
    state: PropTypes.string.isRequired,
    image: PropTypes.string.isRequired,
    chainId: PropTypes.number.isRequired,
    index: PropTypes.number.isRequired,
    delete: React.PropTypes.func.isRequired
  };

  render () {
    return (
      <div className={s.container}>
        <div className={s.candidate}>
          <CandidateRow name={this.props.name} image={this.props.image} />
        </div>
        <div className={s.place}>
          <span className={s.placeText}>{this.props.place}</span>
        </div>
        <div className={s.state}>
          <span className={s.stateText}>{this.props.state}</span>
        </div>
        <div className={s.rightSide}>
          <div className={s.clearDiv} onClick={() => this.props.delete()}>
          </div>
        </div>
      </div>
    )
  }

}

export default SelectedBox



