import React from 'react'
import { connect } from 'react-redux'
import { actions as gamesActions } from '../../redux/modules/games'
import { actions as statesActions } from '../../redux/modules/states'
import { actions as candidatesActions } from '../../redux/modules/candidates'
import { actions as gameActions } from '../../redux/modules/game'
import s from './GameView.scss'
import Header from '../../components/Header/Header.js'
import Footer from '../../components/Footer/Footer.js'
import GameInfoBar from '../../components/GameInfoBar/GameInfoBar.js'
import CandidateRow from '../../components/CandidateRow/CandidateRow.js'
import SelectBox from '../../components/SelectBox/SelectBox.js'
import SelectedBox from '../../components/SelectedBox/SelectedBox.js'
import SelectsInfoBar from '../../components/SelectsInfoBar/SelectsInfoBar.js'
import { bindActionCreators } from 'redux'
// We define mapStateToProps where we'd normally use
// the @connect decorator so the data requirements are clear upfront, but then
// export the decorated component after the main class definition so
// the component can be tested w/ and w/o being connected.
// See: http://rackt.github.io/redux/docs/recipes/WritingTests.html
const mapStateToProps = (state) => ({
  games: state.games,
  states: state.states,
  candidates: state.candidates,
  selects: state.game
})

const mapDispatchToProps = (dispatch) => ({
  gameActions: bindActionCreators(gamesActions, dispatch),
  statesActions: bindActionCreators(statesActions, dispatch),
  candidatesActions: bindActionCreators(candidatesActions, dispatch),
  game: bindActionCreators(gameActions, dispatch)
})

export class GameView extends React.Component {

  static propTypes = {
    games: React.PropTypes.object.isRequired,
    game: React.PropTypes.object.isRequired,
    selects: React.PropTypes.object.isRequired,
    states: React.PropTypes.object.isRequired,
    candidates: React.PropTypes.object.isRequired,
    gameActions: React.PropTypes.object.isRequired,
    statesActions: React.PropTypes.object.isRequired,
    candidatesActions: React.PropTypes.object.isRequired,
    params: React.PropTypes.object.isRequired
  };

  static contextTypes = {
    location: React.PropTypes.object,
    history: React.PropTypes.object
  };

  componentWillMount () {
    this.props.gameActions.fetchGamesIfNeeded()
    this.props.statesActions.fetchStatesIfNeeded()
    this.props.candidatesActions.fetchCandidatesIfNeeded()
  }

  componentWillReceiveProps () {
    this.props.game.calculateNumberOfCandidates(this.props.params.id)
    if (this.props.selects.didSubmit) {
      this.context.history.pushState(null, '/games')
    }
  }

  componentWillUnmount () {
    this.props.game.clear()
  }

  render () {
    return (
        <div className={s.root}>
          <Header key={'header1'} fixed={false}/>
          <Header key={'header2'} fixed/>
            {this.renderGameInfoBar()}
            {this.renderGame()}
          <Footer />
        </div>
    )
  }

  renderGameInfoBar () {
    if (this.props.games.mappedItems.size > 0) {
      var game = this.props.games.mappedItems.get(this.props.params.id)
      return <GameInfoBar
              key={'GameInfoBar'}
              name={game.name}
              loading={0}
              party={game.party}
              prize={game.prize}
              size={game.maxsize}
              winners={game.winners}
              description={game.description}
              total_points={this.props.selects.total_points}
              entry={game.entry}
              submit={this.props.game}
              game_id={this.props.params.id}
              selects={this.props.selects}/>
    } else {
      return <GameInfoBar key={'GameInfoBar'} loading={1}/>
    }
  }

  renderGame () {
    if (this.props.states.mappedItems.size > 0 && this.props.candidates.mappedItems.size > 0) {
      return (
          <div>
          <div className={s.container}>

            <div className={s.states}>

              <div key={'chaintop'} className={s.chain}>
                  <SelectBox key={'2'} name={'State'} states={this.props.games.mappedItems.get(this.props.params.id).states} statesMap={this.props.states.mappedItems}
                            candidates={this.props.games.mappedItems.get(this.props.params.id).candidates} candidatesMap={this.props.candidates.mappedItems} isTop isBottom/>
              </div>

              {this.renderChain()}

              </div>
          </div>
          </div>
        )
    }
  }

  renderChain () {
    if (this.props.selects.chains.size > 0) {
      return this.props.selects.chains.map(function (c, i, a) {
        return (
          <div key={c.ukey} className={s.chain}>
            <SelectsInfoBar points={c.points} list={c.items}/>
            {this.renderSelectBoxes(c.items, i)}
            <SelectBox key={'2'} chainId={i} states={this.props.games.mappedItems.get(this.props.params.id).states} statesMap={this.props.states.mappedItems}
                    candidates={this.props.games.mappedItems.get(this.props.params.id).candidates} candidatesMap={this.props.candidates.mappedItems} isTop={false} isBottom/>
          </div>
        )
      }.bind(this))
    }
  }

  renderSelectBoxes (c, id) {
    return c.map(function (s, i) {
      return (
        <SelectedBox key={'selectedbox' + i} chainId={id} index={i} name={s.candidate} image={s.image} place={s.place} state={s.state} delete={() => this.props.game.deleteRow(i, id)}/>
      )
    }.bind(this))
  }

  renderCandidates () {
    if (this.props.games.mappedItems.size > 0) {
      var game = this.props.games.mappedItems.get(this.props.params.id)
      return game.candidates.map(function (candidate) {
        // if statement to prevent error from immutable. most likely want to fix this
        if (this.props.candidates.mappedItems.get) {
          var person = this.props.candidates.mappedItems.get(candidate)
          return <CandidateRow key={person._id} name={person.name} party={person.party}/>
        }
      }.bind(this))
    }
  }

  renderStates () {
    if (this.props.games.mappedItems.size > 0) {
      var game = this.props.games.mappedItems.get(this.props.params.id)
      return game.states.map(function (state) {
        // if statement to prevent error from immutable. most likely want to fix this
        if (this.props.candidates.mappedItems.get) {
          var place = this.props.states.mappedItems.get(state)
          console.log('right now', this.props.games.mappedItems.get(this.props.params.id))
          return <SelectBox name={place.name} date={place.deadline} states={this.props.states}/>
        }
      }.bind(this))
    }
  }

  renderRemainingStates () {
    if (this.props.games.mappedItems.size > 0) {
      var game = this.props.games.mappedItems.get(this.props.params.id)
      return game.states.map(function (state) {
        // if statement to prevent error from immutable. most likely want to fix this
        if (this.props.candidates.mappedItems.get) {
          var place = this.props.states.mappedItems.get(state)
          return <CandidateRow key={place._id} name={place.name}/>
        }
      }.bind(this))
    }
  }

}

export default connect(mapStateToProps, mapDispatchToProps)(GameView)



