import React, { Component, PropTypes } from 'react'
import s from './SelectsInfoBar.scss'

class SelectedBox extends Component {

  static propTypes = {
    list: PropTypes.object,
    points: PropTypes.number
  };

  render () {
    return (
      <div className={s.container}>
      <div className={s.prediction}>
        <span className={s.clauseText}>PREDICTION:</span>
      </div>
        <div className={s.statement}>
          <span className={s.statementText}>{this.renderPrediction()}</span>
        </div>
        <div className={s.points}>
          <div className={s.clause}>
            <span className={s.clauseText}>PREDICTION TRUE:</span>
            <span className={s.pointsText}>{'+ ' + this.props.points + ' points'}</span>
          </div>
          <div className={s.clause}>
            <span className={s.clauseText}>PREDICTION FALSE:</span>
            <span className={s.pointsText}>{'- ' + this.props.points + ' points'}</span>
          </div>
        </div>
      </div>
    )
  }

  renderPrediction () {
    return this.props.list.map(function (l, i, a) {
      return (
        <span key={'predictionspan' + i} className={s.innerspan}>
          {l.sentence + this.renderPeriod(i, a)}
          {this.renderAnd(i, a)}
        </span>
      )
    }.bind(this))
  }

  renderPeriod (i, a) {
    if (a.size - 1 === i) {
      return '.'
    } else {
      return ''
    }
  }

  renderAnd (i, a) {
    if (a.size - 1 !== i) {
      return <b className={s.bold}>AND</b>
    }
  }
}

export default SelectedBox



